import { useContext } from "react";
import ViewPortMode from "../contexts/ViewPortMode";

function PauseButton({ onClick }) {

    const viewPortMode = useContext(ViewPortMode);

    let width = 16;
    let height = 16;

    switch (viewPortMode.mode) {
        case 'S':
            width = 14; 
            height = 14;
            break;    
        default:
            width = 16; 
            height = 16;
            break; 
    }

    return(
        <button className='button button_type_pause' onClick={onClick}>
            <svg width={width} height={height} viewBox="0 0 14 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path fill-rule="evenodd" clip-rule="evenodd" d="M1 0C0.447266 0 0 0.447266 0 1V17C0 17.5527 0.447266 18 1 18H3C3.55273 18 4 17.5527 4 17V1C4 0.447266 3.55273 0 3 0H1ZM11 0C10.4473 0 10 0.447266 10 1V17C10 17.5527 10.4473 18 11 18H13C13.5527 18 14 17.5527 14 17V1C14 0.447266 13.5527 0 13 0H11Z" fill="white"/>
            </svg>
        </button>
    )
} 

export default PauseButton;